.header {
    position: relative;
    width: 100%; }

.page-nav {
    position: absolute;
    top: 0;
    left: 0;
    padding: 15px 20px;
    color: $white;
    background-color: transparent;
    font-family: $font-family;
    &__expansion-container {
        display: none;
        height: 100vh; }
    &__menu-control {
       padding: 10px;
       font-size: 16px; }
    &__menu-icon {
        vertical-align: sub;
        font-size: 20px; }

    &__menu {
        list-style-type: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: calc(100% - 40px); }
    &__nav-item {
        margin: 12px 0;
        width: 100%; }
    &__nav-link {
        text-align: center;
        display: block;
        padding: 10px;
        font-size: 30px; }

    &--opened {
        width: 100%;
        display: block;
        background-color: $menu-black;

        .page-nav__expansion-container {
            display: block; }

        .page-nav__menu-open {}
        #menu-open {
            display: none; } } }



